  .BookingPage {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
 
    color: rgb(247, 0, 255);
    
   } 
  

  .BookingPage .booking-left {
    flex: 1; 
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center;
    margin-right: 20px; 
  }
  
 
  .BookingPage .booking-right {
    flex: 1;
    padding: 20px;
    text-align: center;
  }
  
 
  .BookingPage .seat-layout {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    
  }
  

.booked {
  background-color: red;
  color: white;
}

.selected {
  background-color: green;
  color: white;
}

.available {
  background-color: white;
  color: black;
}
.status-button {
  padding: 10px;
  margin-right: 10px;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}
.available {
  background-color: blue; 
}