.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 70px; 
    background-color: #333; 
    color: #fff; 
   
    z-index: 1000; 
    transition: top 0.3s;
}


.logo {
    text-align: center;
    margin-bottom: 20px;
}

.logo h2 {
    font-size: 24px;
    margin: 0;
    color: #fff;
}


ul {
    list-style: none;
    padding: 0;
}

li {
    margin-bottom: 10px;
}


li a {
    display: block;
    padding: 10px 20px;
    text-decoration: none;
    color: #fff;
    transition: background-color 0.3s;
}

/* Sidebar link hover effect */
li a:hover {
    background-color: #555; 
}

/* Active link styling */
li.active a {
    background-color: #007bff; 
}

/* Main content container */
.main-content {
    margin-left: 250px; 
    padding: 20px;
  }

/* Media query for smaller screens */
@media (max-width: 768px) {
    .sidebar {
      width: 0; 
    }
  
    .main-content {
      margin-left: 0; 
    }
  }